<template>
    <page>
        <header>
            <h1>AUT-NS</h1>
        </header>
        <main v-if="apiKeySet">
            <input v-model="query" type="text" placeholder="Train NR or 'station to station'" />
            <button @click="performQuery()">Search</button>
            <button @click="dynamicInfo()">Refresh dynamic info</button>
            <section v-if="resultType === 'train'" class="searchResults">
                <section class="trainSections">
                    <img v-for="trainPart in queryResult.materieeldelen" :key="trainPart.materieelnummer" :src="trainPart.afbeelding" alt="Train image" class="fullTrainImage">
                </section>
                <section class="trainSigns">
                    <div class="railSignWrapper">
                        <div class="railStopAtSign">
                            <p>{{ queryResult.lengte }}</p>
                        </div>
                    </div>
                    <div class="materialNR" v-for="materialPart in filteredMaterialParts" :key="materialPart.materieelnummer">
                        <div class="materialNRInner">
                            <p>
                                {{ materialPart.materieelnummer }}
                                <span class="facility mdi" :class="facilityIcon(facility)" v-for="facility in materialPart.faciliteiten" :key="facility + Math.random()"></span>
                            </p>
                        </div>
                    </div>
                </section>
            </section>
            <section v-if="resultType === 'error'">
                <p>{{ errorResult }}</p>
            </section>
            <section class="stationDepartures">
                <h1>Station {{ nearestStation.namen.lang }}</h1>

                <div class="departure" v-for="departure in departures" :key="departure.id" @click="viewDeparture(departure)">
                    <p>
                        <span class="actualTime">{{ showDepartureTime(departure.actualDateTime) }} </span>
                        <span class="changedOldTime" v-if="!asScheduled(departure)">{{ showDepartureTime(departure.plannedDateTime) }}</span>
                        <span class="mdi mdi-check" v-if="departure.departureStatus === 'ON_STATION' && asScheduled(departure)"></span>
                        <span class="mdi mdi-alert-circle-outline" v-if="departure.departureStatus === 'ON_STATION' && !asScheduled(departure)"></span>
                        <span class="mdi mdi-clock-check-outline" v-if="departure.departureStatus === 'INCOMING' && asScheduled(departure)"></span>
                        <span class="mdi mdi-clock-alert-outline" v-if="departure.departureStatus === 'INCOMING' && !asScheduled(departure)"></span>
                        {{ departure.product.categoryCode}}
                        <span> {{ departure.direction }} </span>
                        {{ departure.product.number }}, spoor {{ departure.plannedTrack }}
                    </p>
                    <div v-if="departure.messages !== undefined">
                        <div class="info" v-for="message in departure.messages" :key="message + Math.random()">
                            <p>
                                <span class="mdi mdi-alert-circle-outline redAlert"></span>
                                {{ message.message }}
                            </p>
                        </div>
                    </div>
                    <div v-else>
                        <div class="info noInfo">
                            <p>
                                <span class="mdi mdi-information-outline"></span>
                                Geen extra informatie
                            </p>
                        </div>
                    </div>
                </div>
                <!-- <p>Lat: {{ lat }}</p>
                <p>Long: {{ long }}</p>
                <p>Has GPS: {{ hasGPS }}</p>
                <p>Status: {{ status }}</p> -->
            </section>
            <button @click="apiKeySet = false">Change API Token</button>
        </main>
        <main v-else>
            <input v-model="apiKey" type="text" placeholder="API Token" />
            <button @click="setApiKey()">Set</button>
        </main>
    </page>
</template>

<script>
import vueChannel from "vue-channel";

import page from "../components/page.vue";

import nsCall from "../js/nsCall.js";
import stationsQueryable from "../json/stationsSearchable.json";

window.nsCall = nsCall;

function getStationCodeFromQuery(query) {
    var exactMatchAvailableAt = -1;
    var matchedStation = {};
    var queried = stationsQueryable.queryable.filter((station, index) => {
        if (station.stationName.toLowerCase() === query) {
            exactMatchAvailableAt = index;
        }

        return station.stationName.toLowerCase().includes(query);
    });

    if (exactMatchAvailableAt !== -1) {
        console.log(exactMatchAvailableAt);
        matchedStation = stationsQueryable.queryable[exactMatchAvailableAt];
    } else {
        matchedStation = queried[0];
    }

    return matchedStation;
}

export default {
    components: {
        page
    },
    data() {
        return {
            query: "",
            apiKey: "",
            apiKeySet: false,
            resultType: "none",
            queryResult: {},
            nearestStation: {
                namen: {
                    lang: ""
                }
            },
            lat: "",
            long: "",
            hasGPS: false,
            status: "None",
            departures: []
        }
    },
    computed: {
        filteredMaterialParts() {
            return this.queryResult.materieeldelen.filter(part => part.materieelnummer !== undefined);
        }
    },
    methods: {
        performQuery() {
            var query = this.query.toLowerCase();
            this.resultsError = false;

            if (!isNaN(query)) {
                nsCall("/virtualTrain/" + this.query).then(responce => {
                    console.log(responce);

                    this.resultType = "train";
                    this.queryResult = Object.assign({}, this.queryResult, responce);
                }).catch(err => {
                    this.resultType = "error";
                    this.queryResult = Object.assign({}, this.queryResult, {err});
                });
            } else {
                query = query.split(" to ");

                // console.log(getStationCodeFromQuery(query[0]));
                // console.log(getStationCodeFromQuery(query[1]));
                nsCall("/trips", {
                    fromStation: getStationCodeFromQuery(query[0]).stationCode,
                    toStation: getStationCodeFromQuery(query[1]).stationCode
                }).then(responce => {
                    console.log(responce);
                });
            }
        },
        setApiKey() {
            localStorage.setItem("apiKey", this.apiKey);
            this.apiKeySet = true;

            vueChannel("apiKey")
                .set({
                    apiKey: this.apiKey
                });
        },
        dynamicInfo() {
            this.status = "Checking dynamic info";

            if (navigator !== undefined) {
                this.status = "Has navigator";
            }

            if ("navigator" in window && "geolocation" in window.navigator) {
                this.status = "Has requirements";
                navigator.geolocation.getCurrentPosition(pos => {
                    var { coords } = pos;

                    this.hasGPS = true;

                    this.lat = coords.latitude;
                    this.long = coords.longitude;

                    var stationDistances = stationsQueryable.map(station => {
                        var distanceLat = coords.latitude - station.lat;
                        var distanceLong = coords.longitude - station.lng;

                        var directDistance = Math.sqrt((distanceLat ** 2) + (distanceLong ** 2));

                        station.directDistance = directDistance;

                        return station;
                    });

                    stationDistances.sort((a, b) => {
                        if (a.directDistance < b.directDistance) {
                            return -1;
                        } else {
                            return 1;
                        }
                    });

                    // stationDistances[0].code = "AMR";

                    nsCall("/departures/" + stationDistances[0].code)
                        .then(response => {
                            var departures = response.payload.departures.map(departure => {
                                return {
                                    ...departure,
                                    id: "r" + Math.random()
                                }
                            });

                            this.departures = departures;
                        });

                    this.nearestStation = Object.assign({}, this.nearestStation, stationDistances[0]);
                });
            }
        },
        showDepartureTime(time) {
            var date = new Date(time);

            var fixedLengthNumber = number => {
                if (number < 10) {
                    return "0" + number;
                } else {
                    return number;
                }
            }

            return fixedLengthNumber(date.getHours()) + ":" + fixedLengthNumber(date.getMinutes());
        },
        viewDeparture(departure) {
            this.query = departure.product.number;

            this.performQuery();
        },
        asScheduled(departure) {
            return departure.actualDateTime === departure.plannedDateTime;
        },
        facilityIcon(facility) {
            if (facility === "WIFI") {
                return ["mdi-wifi"];
            } else if (facility === "TOILET") {
                return ["mdi-toilet"];
            } else if (facility === "STILTE") {
                return ["mdi-volume-off"];
            } else if (facility === "FIETS") {
                return ["mdi-bicycle"];
            } else if (facility === "STROOM") {
                return ["mdi-power-plug"];
            } 
        }
    },
    created() {
        var apiKey = localStorage.getItem("apiKey");

        if (apiKey) {
            vueChannel("apiKey")
                .set({
                    apiKey
                });

            this.apiKeySet = true;
            this.apiKey = apiKey;
        }

        this.dynamicInfo();

        var dynamicRefreshLimit = 100;
        var currentRefreshCount = 0;

        if (!window.refreshActive) {
            setInterval(() => {
                if (currentRefreshCount < dynamicRefreshLimit) {
                    currentRefreshCount++;
                    this.dynamicInfo();
                    console.log("DYNAMIC INFO");
                }
            }, 15000);
        }

        window.refreshActive = true;
    }
}
</script>

<style lang="scss" scoped>

@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap');

.inner > header {
    width: 100%;
    height: 64px;

    background-color: #5e35b1;

    h1 {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        padding: 0px 16px;

        font-size: 24px;
        color: #fff;
        font-family: "Montserrat", sans-serif;
    }
}

main {
    h2 {
        font-family: "Montserrat", sans-serif;
        padding: 0px 16px;
    }

    input {
        width: 90%;

        display: block;
        margin: 8px auto;
        padding: 8px;

        font-size: 18px;
        outline: 0;
        border-radius: 5px;
        border: 2px solid #2196f3;
    }

    button {
        width: calc(90% + 20px);

        display: block;
        margin: 8px auto;
        padding: 8px;

        font-size: 18px;
        outline: 0;
        border-radius: 5px;
        border: 2px solid #2196f3;
        background-color: #2196f3;
        cursor: pointer;
    }

    section.searchResults {
        section {

            $signResultsHeight: 64px;

            // height: $signResultsHeight;

            border-top: 1px solid #d3d3d3;
            border-bottom: 1px solid #d3d3d3;

            .trainPartDetail {
                width: 95%;

                padding: 16px;
                margin: 8px auto;
                border: #acacac 2px solid;
                border-radius: 5px;
            }

            &.trainSections {
                height: unset;

                display: inline-block;
                padding: 8px 0px;
            }

            &.trainSigns {
                width: 100%;

                display: inline-block;
            }

            .fullTrainImage {
                max-height: 100px;
                max-width: 95%;

                display: block;
                margin: auto;
            }

            .railSignWrapper {
                height: $signResultsHeight;
                width: $signResultsHeight;

                float: left;

                .railStopAtSign {
                    height: 50%;
                    width: 50%;

                    position: relative;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) rotate(45deg);

                    background-color: #2222ff;
                    border: 2px solid #dddddd;
                    border-radius: 5px;

                    p {
                        position: relative;
                        top: 50%;
                        margin: 0;
                        transform: translateY(-50%) rotate(-45deg);

                        text-align: center;
                        color: #ffffff;
                        font-size: 24px;
                        font-family: "Source Code Pro", monospace;
                    }
                }
            }

            .materialNR {
                height: $signResultsHeight;
                padding: 0px 8px;

                float: left;

                .materialNRInner {
                    height: 40%;
                    // width: 90%;
                    
                    position: relative;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    padding: 4px;

                    background-color: transparent;
                    border: #1e65ff 2px solid;

                    p {
                        position: relative;
                        top: 50%;
                        transform: translateY(-50%);
                        margin: 0;

                        text-align: center;
                        color: #1e65ff;
                        font-size: 24px;
                        font-family: "Source Code Pro", monospace;
                        font-weight: 700;

                        .facility {
                            float: right;
                            margin-left: 6px;
                        }
                    }
                }
            }
        }
    }

    .stationDepartures {
        border-top: 1px solid #d3d3d3;
        border-bottom: 1px solid #d3d3d3;

        h1 {
            margin: 8px auto;

            text-align: center;
            font-family: "Roboto", sans-serif;
            font-weight: 400;
            color: #363636;
        }

        .departure {
            padding: 8px 16px;

            cursor: pointer;

            p {
                margin: 0;
                
                font-family: "Roboto", sans-serif;
                font-weight: 500;

                span {
                    font-weight: 700;

                    &.actualTime {
                        color: #099910;
                    }

                    &.changedOldTime {
                        color: #992609;
                        text-decoration: line-through;
                    }
                }
            }

            .info {
                color: #494949;

                &.noInfo {
                    color: #8d8d8d;
                }

                .redAlert {
                    color: #992609;
                }
            }
        }
    }
}

</style>