import vueChannel from "vue-channel";

var baseEndpoint = "https://aut-ns-backend.rster2002.now.sh"
// var baseEndpoint = "http://localhost:5000";
// var baseEndpoint = "http://192.168.1.65:5000";

function nsCall(endpoint, queryObject) {
    return new Promise((res, rej) => {
        vueChannel("apiKey")
            .disposable(state => {
                if (state.apiKey !== undefined) {
                    
                    endpoint = baseEndpoint + endpoint + "?apiToken=" + state.apiKey;

                    if (queryObject !== undefined) {
                        var queryArguments = Object.entries(queryObject);

                        queryArguments.forEach(argument => {
                            endpoint += "&" + argument[0] + "=" + argument[1];
                        });
                    }

                    console.log(endpoint);

                    fetch(endpoint)
                        .then(r => {
                            return r.json()
                        })
                        .then(json => res(json))
                        .catch(err => rej(err));
                } else {
                    return true;
                }
            });
    });
}

export default nsCall;